@import 'variables';

// mobile
@media (min-width: $breakpoint-m-min) and (max-width: $breakpoint-m-max) {
  .content-title {
    margin-top: 5rem;
    display: flex;
    justify-content: center;
  }

  #project-container {
    margin: 1.5rem;
    position: relative;
    width: 22.5em;

    .prjt-img {
      width: 100%;
      height: 22.5em;
    }

    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.6);
      font-family: inherit;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      opacity: 0;
      transition: opacity 0.25s;

      .overlay_title {
        font-size: 2em;
        font-weight: bold;
      }
      .overlay_p {
        font-size: 1.25em;
        margin-top: 0.25em;
      }
    }
    .overlay:hover {
      backdrop-filter: blur(5px);
      opacity: 1;
    }

    .overlay_btn {
      display: flex;
      justify-content: space-evenly;
      color: white;
      text-decoration: none;
    }
  }
}
